@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Raleway:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.all-contents{
  
  display: flex;
  flex-direction: row;
  height: 100vh;
  width: 100vw;
  color: white;
  
 
}




.left-sidebar{
  width:17%;
  height: 100%;
  position: fixed;
  top: 0;
  float:left;
  overflow: auto;
  flex-shrink: 0;
  position: fixed;
  top: 0;
}

.right-column{
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-left: 17%;
  height: 100%;
  overflow-y: hidden;
}

.top{
  background: url(/site/static/media/cypress-darkened.73d8e624.jpg) no-repeat center center fixed;
  background-size: cover;
  width: 100%;
  position: fixed;
  top:0;
  z-index: 10;
  
}

.page{
  
  
 
  height: 100%;
  padding-top: calc(20% + 65px);
  z-index: 1;
  padding-bottom: 40px;
  overflow-y: auto;
  background: rgba(255, 255, 255, 0.95);
  background-size: cover;
  
  
  
  
}



.menuBar{
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}


.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Universal Styles */

html {
  
  font-size: calc(5px + 1.5vw);
  background: url(/site/static/media/cypress_background.de1e9254.jpg) no-repeat center center fixed;
    background-size: cover;
}

@media only screen and (max-width: 560px) {
  html {
    
    background: url(/site/static/media/cypress_background.de1e9254.jpg) no-repeat center center fixed;
    background-size: cover;

    
  }
}

input:focus {
  outline: none;
}

/* Reusable Component - Image Container */

.image-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  background: #000;
}

.image-container img {
  width: 100%;
}

/* App Styles */

.menuBar, h1, h2, .large-print{
  font-family: 'Abril Fatface', cursive;
  margin: 0px;
}

h3{
  font-family: 'Abril Fatface', cursive;
  
  margin-bottom: 0px;
  
  padding-bottom:5px;
}

p, .contact, .not-bold
.title {
  font-family: 'Raleway', sans-serif;
  
} 



.app-content {
  margin: 10px;
  color: black;
 
}


.menuBar {
  
  width: auto;
  
  margin: 0;
  padding: 0;
  background-color: rgb(73, 121, 136, 0.8);
  /*background-image: url('../App/sunny.jpg');
  background-size: cover;
  background-repeat: no-repeat aha;
  */
  }
 

  
  .menuOptions ul {
    display: flex;
    flex-direction: column;
    justify-content: top;
    margin: 0px;
    padding: 0.8vw;
    flex: auto;
    list-style-type: none;
    
  }
  
  .menuOptions a{
    cursor: pointer;
    
    font-size: 2.2vw;
    padding: 35px 15px 15px 5px;
    line-height: 1;
    text-align: left;
    font-weight: 600;
    transition: color .75s;
    display: flex;
    text-decoration: none;
    
  }
  .menu_item a {
    color: black;
  }
  
  .menu_item_active a{
    
    border-bottom:  solid #f0c36c;
    color: #f0c36c;
  }

  .menuOptions a:hover {
    color: #d4cfcf;
  }

 
.profile{
    display: flex;
    height: auto;
    width: auto;
    vertical-align: top;
    padding: 10px 10px 10px 0.6rem;
    margin: 0px;

   
    top: 0;
}



#headshot{
    
    width: auto;
    height: 10em;
    margin-left: 5px;
    margin-right: 5px;
    border-radius: 10px 30px;
    
}

.info{
    float: right;
    display: inline-block;
    margin-left: 0.5em;
    margin-top: 0px !important;
    width: 50%;
    
    
    
}



.social-icon{
    margin: 0px .5rem 1rem 0px;
    background-color: transparent;
    
}

.social-links img{
    width: 1.2rem;
    height: 1.2rem;

}

h1 {
    
    padding: 0;
    text-align: left;
    position: static;
    
  
    color: #fff;
  }

.app-content{
    color: black;
    padding-left: calc(4px + .6rem);
    
    width: 80%;
    height: auto;
}
ul{
    margin: 0;
}
